import React, { useState, useEffect } from "react";
import "../css/profileCss.scss";
import "../css/profileadd.scss";
import ProfileChoose from "../components/desktop/profile/ProfileChoose";
import ProfileAddOrRegist from "../components/desktop/profile/ProfileAddOrRegist";

const Profile: React.FC = () => {
    const [regis, setRegis] = useState(false);  // 아이 등록 상태 여부
    const [profileData, setProfileData] = useState<any[]>([]);  // 프로필 데이터 배열

    useEffect(() => {
        const jwtToken = localStorage.getItem('jwtToken');
        const email = localStorage.getItem('email');  // 로컬 스토리지에서 이메일 가져옴

        if (jwtToken && email) {
            // 백엔드에서 프로필 정보 가져오기
            fetch(`http://localhost:8080/api/profiles/${email}`, {
                method: 'GET',
                headers: {
                    'Authorization': `Bearer ${jwtToken}`,
                    'Content-Type': 'application/json',
                },
            })
                .then(response => {
                    if (!response.ok) {
                        throw new Error('Failed to fetch profile data');
                    }
                    return response.json();
                })
                .then(data => {
                    setProfileData(data);  // 가져온 프로필 데이터를 상태로 설정
                })
                .catch(error => console.error('Error fetching profile data:', error));
        }
    }, []);


    return (
        <div className={"pc-profile-body"}>
            <div className={"pc-profile-box"}>
                {
                    !regis ?
                        <ProfileChoose setRegis={setRegis} profileData={profileData} />  // 프로필 데이터 전달
                        :
                        <ProfileAddOrRegist setRegis={setRegis} />
                }
            </div>
        </div>
    );
};

export default Profile;
