import React, { useState } from "react";
import { Button, TextField } from "@mui/material";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
import dayjs, { Dayjs } from "dayjs";  // Dayjs 라이브러리 임포트

interface ProfileProps {
    setRegis: React.Dispatch<React.SetStateAction<boolean>>;
}

const ProfileAddOrRegist: React.FC<ProfileProps> = ({ setRegis }) => {
    const [selectedImage, setSelectedImage] = useState<string | null>(null);
    const [name, setName] = useState("");
    const [birthdate, setBirthdate] = useState<Dayjs | null>(null);
    const [gender, setGender] = useState("");

    const handleImageChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const file = event.target.files?.[0];
        if (file) {
            const reader = new FileReader();
            reader.onloadend = () => {
                setSelectedImage(reader.result as string);
            };
            reader.readAsDataURL(file);
        }
    };

    const handleSubmit = () => {
        const email = localStorage.getItem("email");
        const jwtToken = localStorage.getItem("jwtToken");

        const profileData = {
            email,
            name,
            birthdate: birthdate ? birthdate.toDate() : null,  // Dayjs 객체를 Date로 변환
            gender,
            imageUrl: selectedImage,
        };

        fetch('http://localhost:8080/api/profiles', {
            method: "POST",
            headers: {
                "Authorization": `Bearer ${jwtToken}`,
                "Content-Type": "application/json",
            },
            body: JSON.stringify(profileData),
        })
            .then(response => response.json())
            .then(data => {
                console.log("Profile registered:", data);
                setRegis(false);  // 등록 완료 후 프로필 선택 화면으로 돌아감
            })
            .catch(error => console.error("Error registering profile:", error));
    };

    return (
        <>
            <div className={"pc-profile-ar-title"}>
                <span>아이 등록</span>
            </div>

            <div className={"pc-profile-ar-close"} onClick={() => setRegis(false)}>
                <img src={"/img/close.png"} alt={""} />
            </div>

            <div className={"pc-profile-ar-body"}>
                <div className={"pc-profile-ar-register-img-box"} onClick={() => document.getElementById('image-upload')?.click()}>
                    <div className={"pc-profile-ar-register-img"}>
                        <img src={selectedImage || "/img/addChild.png"} alt={""} />
                    </div>

                    <span>아이 등록</span>
                    <input
                        type="file"
                        id="image-upload"
                        accept="image/*"
                        style={{ display: "none" }}
                        onChange={handleImageChange}
                    />
                </div>

                <div className={"pc-profile-ar-input-list"}>
                    <div className={"pc-profile-ar-input-box"}>
                        <div className={"pc-profile-ar-input-title"}>
                            <span>이름/ 애칭</span>
                            <span className={"pc-profile-ar-star"}>*</span>
                        </div>
                        <TextField value={name} onChange={(e) => setName(e.target.value)} />
                    </div>

                    <div className={"pc-profile-ar-input-box"}>
                        <div className={"pc-profile-ar-input-title"}>
                            <span>생년 월일</span>
                            <span className={"pc-profile-ar-star"}>*</span>
                        </div>

                        <LocalizationProvider dateAdapter={AdapterDayjs}>
                            <DatePicker
                                value={birthdate}
                                onChange={(date) => setBirthdate(date)}
                            />
                        </LocalizationProvider>
                    </div>

                    <div className={"pc-profile-ar-input-box"}>
                        <div className={"pc-profile-ar-input-title"}>
                            <span>성별</span>
                            <span className={"pc-profile-ar-star"}>*</span>
                        </div>

                        <div className={"pc-profile-ar-choose-sex"}>
                            <Button className="MuiButton-contained" variant="contained" onClick={() => setGender("남자")}>남자</Button>
                            <Button className="MuiButton-outlined" variant="outlined" onClick={() => setGender("여자")}>여자</Button>
                        </div>
                    </div>
                </div>
            </div>

            <div>
                <Button id="register-button" variant="contained" size={"large"} onClick={handleSubmit}>등록 완료</Button>
            </div>
        </>
    );
}

export default ProfileAddOrRegist;
