import React, { useEffect, useState } from 'react';
import { GoogleOAuthProvider, GoogleLogin } from '@react-oauth/google';
import '../css/loginCss.scss';
import { useNavigate } from "react-router-dom";

const LoginPage: React.FC = () => {
    const [id, setId] = useState('');  // 수동 로그인용 상태
    const [password, setPassword] = useState('');
    const [rememberMe, setRememberMe] = useState(false);

    const navigate = useNavigate();

    // JWT가 존재하는지 확인하고, 유효하면 바로 프로필 페이지로 이동
    useEffect(() => {
        const jwtToken = localStorage.getItem('jwtToken');  // 로컬 스토리지에서 JWT 확인

        if (jwtToken) {
            // 백엔드로 JWT 유효성 검증 요청
            fetch('http://localhost:8080/api/validate-token', {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${jwtToken}`,  // Bearer 토큰 포함
                    'Content-Type': 'application/json',
                }
            })
                .then(response => {
                    if (response.ok) {
                        // JWT가 유효하면 프로필 페이지로 이동
                        navigate('/profile');
                    } else {
                        console.error('Token validation failed: ', response.status);
                        throw new Error('Unauthorized');
                    }
                })
                .catch(error => {
                    console.error('Error validating token:', error);
                });
        } else {
            console.log('JWT Token is missing');
        }
    }, [navigate]);

    // 수동 로그인 처리
    const handleLogin = (e: React.FormEvent) => {
        e.preventDefault();
        const loginData = { id, password };  // 수동 로그인 데이터
        // 백엔드로 수동 로그인 요청
        fetch('http://localhost:8080/api/manual-login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(loginData),
        })
            .then(async response => {
                if (!response.ok) {
                    const errorText = await response.text();
                    throw new Error(errorText);
                }
                return response.json();
            })
            .then(data => {
                const jwtToken = data.token;  // 백엔드에서 반환된 JWT 토큰
                const email = data.email;

                // JWT 토큰 및 이메일을 로컬 스토리지에 저장
                localStorage.setItem('jwtToken', jwtToken);
                localStorage.setItem('email', email);

                // 프로필 페이지로 이동
                navigate("/profile");
            })
            .catch(error => console.error('Error during manual login:', error));
    };

    // 구글 로그인 성공 시 처리
    const handleGoogleLoginSuccess = (credentialResponse: any) => {
        const idToken = credentialResponse.credential;  // 구글에서 제공한 ID 토큰

        // 백엔드로 POST 요청을 보내어 JWT 요청
        fetch('http://localhost:8080/api/google-login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            credentials: 'include',  // 쿠키 및 인증 정보 포함
            body: JSON.stringify({ token: idToken }),  // ID 토큰 전달
        })
            .then(async response => {
                if (!response.ok) {
                    const errorText = await response.text();
                    throw new Error(errorText);
                }
                return response.json();
            })
            .then(data => {
                const jwtToken = data.token;  // 백엔드에서 반환된 JWT 토큰
                const email = data.email;

                // JWT 토큰 및 이메일을 로컬 스토리지에 저장
                localStorage.setItem('jwtToken', jwtToken);
                localStorage.setItem('email', email);

                // 프로필 페이지로 이동
                navigate("/profile");
            })
            .catch(error => console.error('Error during Google login:', error));
    };

    // 구글 로그인 실패 시 처리
    const handleGoogleLoginFailure = () => {
        console.log('Google Login Failed');
    };

    return (
        <GoogleOAuthProvider clientId="597389730622-1qsc90pogdcg8crjid8qsd0d20t2f1vi.apps.googleusercontent.com">
            <div className="login-page">
                <div className="login-container">
                    <div className="image-container">
                        <img src="/img/mainPaint.png" alt="Mother and child with onion character" className="login-image" />
                        <p className="image-caption">"이번 생에 부모는 처음이니까"<br />저희가 함께 도와드리겠습니다.</p>
                    </div>
                    <div className="form-container">
                        <img src="/img/logo.png" alt="YANGPA Logo" className="logo"/>
                        <form onSubmit={handleLogin}>
                            <input
                                type="text"
                                placeholder="아이디"
                                value={id}
                                onChange={(e) => setId(e.target.value)}
                            />
                            <input
                                type="password"
                                placeholder="비밀번호"
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                            />
                            <div className="remember-me">
                                <input
                                    type="checkbox"
                                    id="rememberMe"
                                    checked={rememberMe}
                                    onChange={(e) => setRememberMe(e.target.checked)}
                                />
                                <label htmlFor="rememberMe">자동 로그인</label>
                            </div>
                            <div className="findIDFW">
                                <GoogleLogin
                                    onSuccess={handleGoogleLoginSuccess}  // 구글 로그인 성공 시 호출
                                    onError={handleGoogleLoginFailure}    // 구글 로그인 실패 시 호출
                                    text="signin_with"
                                    shape="pill"
                                />
                            </div>
                            <div className="btnArea">
                                <button type="button" className="register-button">회원가입</button>
                                <button type="submit" className="login-button">로그인</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </GoogleOAuthProvider>
    );
};

export default LoginPage;
